import React from 'react';

function progressBar({color,percent,reward,progress}) {
  const remaining = 100 - progress
  return (
      <div style={{width:'70%'}} className='my-2' >
      <div className='progressbar' style={{display:'flex',justifyContent:'space-between',border:`5px solid ${color}`,
       borderRadius:'12px',padding:'1rem 1.5rem',background:`linear-gradient(90deg, ${color} ${progress}%,${progress}%, transparent ${remaining}%,transparent 100%)`}}>
          <p className='progressItem m-0' style={{color:'rgba(0,0,0)'}} >{percent}</p>
          <p className='progressItem m-0' style={{color:progress!==100?'white':'rgba(0,0,0)'}}>{reward}</p>
      </div>
      </div>
  );
}

export default progressBar;
