import React from 'react';
import {Navbar,Nav,Container} from 'react-bootstrap'
import {useNavigate} from "react-router-dom";

import instaLogo from '../images/socials/instagram.png'
import discordLogo from '../images/socials/discord.png'
import twitterLogo from '../images/socials/twitter.png'

function Header() {
  let navigate = useNavigate();
  const anchorClicked = (id) =>{
    if(id === 'MINT' ){ navigate(`/`, { replace: true }); }
    if(id === 'ABOUT' ){ navigate(`/about`, { replace: true }); }
    if(id === 'ROADMAP' ){ navigate(`/roadmap`, { replace: true }); }
    if(id === 'TEAM' ){ navigate(`/team`, { replace: true }); }
    if(id === 'FAQS' ){ navigate(`/FAQS`, { replace: true }); }
  }
  return (
    <>
      <Navbar className='bg' expand="lg">
        <Container fluid>
          <Navbar.Brand className='brandG' href="#">G</Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0 navItem"
              navbarScroll
              style={{width:'100%'}}
            >
              <Nav.Link id='MINT' className='navLink para MINT' onClick={()=>anchorClicked('MINT')} href="#action1">MINT</Nav.Link>
              <Nav.Link id='ABOUT' className='navLink para ABOUT '  onClick={()=>anchorClicked('ABOUT')} href="#action2">ABOUT</Nav.Link>
              <Nav.Link id='ROADMAP' className='navLink para ROADMAP'  onClick={()=>anchorClicked('ROADMAP')} href="#action3">ROADMAP</Nav.Link>
              <Nav.Link id='TEAM' className='para TEAM navLink'  onClick={()=>anchorClicked('TEAM')} href="#action4">TEAM</Nav.Link>
              <Nav.Link id='FAQS' className='para FAQS navLink'  onClick={()=>anchorClicked('FAQS')} href="#action5">FAQS</Nav.Link>
              <div className='social-holder'>
                <img onClick={()=>{window.open('https://discord.gg/HjGZU7NFCM')}} src={discordLogo}/>
                <img onClick={()=>{window.open('https://www.instagram.com/gamers_nfts_/')}} src={instaLogo}/>
                <img onClick={()=>{window.open('https://twitter.com/GAMERS_NFTS_')}} src={twitterLogo}/>

              </div>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  );
}

export default Header;
