import React, { useEffect,useState } from 'react';
import SiderShape from '../components/SiderShape';
import gamers from '../images/main-gif.gif';
import { Button } from 'react-bootstrap'


import { useDispatch, useSelector } from "react-redux";
import { connect } from "../redux/blockchain/blockchainActions";
import { fetchData } from "../redux/data/dataActions";


import { toast } from 'react-toastify';

import Web3EthContract from "web3-eth-contract";

import loading from '../images/loading.svg'

function Mint({color}) {
  const truncate = (input, len) =>
    input.length > len ? `${input.substring(0, len)}...` : input;


  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(`Click buy to mint your NFT.`);
  const [mintAmount, setMintAmount] = useState(1);

  
  const [paused,setPaused] = useState('')
  const [cost,setCost] = useState("")
  const [maxPerWallet,setMaxPerWallet]=useState(0);
  const[totalSupply,setTotalSupply]=useState(0)
  const[refresh,setRefresh]=useState(0)
  const[ethereum,setEthereum]=useState(null)

  const[balanceExceeded,setBalanceExceeded]=useState('')


  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const claimNFTs = () => {
    if(claimingNft)return;
    let cost2 = cost;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost2 * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    toast.success(`Minting your ${mintAmount > 1 ? 'Gamers' : 'Gamer'}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint( mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        toast.error("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        toast.success(() =>
          <span>
            Gamer Mint Successful! Visit <a href='https://opensea.io/'>opensea.io</a> to view it.
          </span>
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
        setRefresh(refresh+1)
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 20) {
      newMintAmount = 20;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();

  }, []);

  useEffect(() => {
    getData();

  }, [blockchain.account]);

  const setAmountInput = (amount) => {
    amount = Number(amount)
    if (isNaN(amount)) return;
    if (amount > 20) {
      setMintAmount(20)
    }
    else {
      setMintAmount(amount)
    }
  }

  const mint = () => {
    claimNFTs();
    getData();
  }

  const connectWallet=()=>{
    if(paused===''&&ethereum===null)return;
    dispatch(connect());
    getData();
  }


  const initConnect=async()=>{
    const { ethereum } = window;
   
    Web3EthContract.setProvider(ethereum);
    const address=CONFIG.CONTRACT_ADDRESS
    const abiResponse = await fetch("/config/abi.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const abi = await abiResponse.json();

    const SmartContractObj = new Web3EthContract(
      abi,
      address
    );
    
    console.log(ethereum)

    if(ethereum!==undefined){
      try{
       
        const cost=await SmartContractObj.methods.cost().call()
       
        const paused=await SmartContractObj.methods.paused().call()
        const totalSupply=await SmartContractObj.methods.totalSupply().call()
        setCost(cost)
    
        setTotalSupply(totalSupply)
   
        setPaused(paused)
        console.log('wow')
      }
      catch(err){
        console.log(err)
      }
    }
    setEthereum(ethereum)
  }

  useEffect(()=>{
    if(CONFIG.CONTRACT_ADDRESS==="")return;
    initConnect()
  },[CONFIG,refresh])

  console.log(totalSupply)
  useEffect(()=>{
    if(blockchain.account===null)return;

    blockchain.smartContract.methods.balanceOf(blockchain.account).call().then((result)=>{
      console.log(typeof(result))
      if(Number(result)>0){
        setBalanceExceeded(false);
      }
      else{
        setBalanceExceeded(false);
      }
    })

  },[blockchain.account])



  return (
      <div>
          <div className='row mx-0 px-0'>
               <div className='col-md-1 mx-0 px-0 shapeContainer w-100' >
                  <SiderShape color={color} />
               </div>
               <div className='col-md-11 col-12'>
                    <div className='' style={{display:'flex',alignItems:'center',flexDirection:'column'}}>
                         <div className='TitleContainer'>
                           <p className='mainTitle'> 
                              <span className='span1'>G</span> 
                              <span className='span2'>A</span>
                              <span className='span3'>M</span>
                              <span className='span4'>E</span>
                              <span className='span5'>R</span>
                              <span className='span6'>S</span>
                           </p>
                           <p className='text-right para' style={{paddingRight:'15px',marginTop:'-20px'}} >
                             BY 990VR, LLC
                           </p>
                         </div>
                         <div className='row' >
                            <div className='col-md-6 col-sm-12 mintContainer'>
                                <img style={{height:'330px'}} src={gamers} className='' />
                            </div>
                            <div className='col-md-6 col-sm-12 mintContainer' style={{display:'flex',alignItems:'end'}}>
                                 <div>
                                   <div style={{display:'flex',alignItems:'center',flexDirection:'column'}}>
                                    
                                    {cost!==""&&
                                      <div style={{display:'flex',alignItems:'center',flexDirection:'column'}}>
                                        <p className='minted para mb-0'>{totalSupply}/20,000</p>
                                        <p className='minted para'>MINTED</p>
                                      </div>
                                    }
                                    {blockchain.account !== null &&

                                      <div className="mt-1 font-m">
                                        <a className="para mt-1" href={CONFIG.SCAN_LINK}>
                                          Contract:
                                          {truncate(CONFIG.CONTRACT_ADDRESS, 15)}
                                        </a>
                                        <div className="para mt-1" >
                                          Connected AS:
                                          {blockchain?.account?.substring(0,8)}...{blockchain?.account?.substring(blockchain.account.length-8,blockchain.account.length)}
                                        </div>
                                        <div className="para mt-1" >
                                          Price: 0.033 eth per nft + gas fee
                                        </div>
                                      </div>
                                    }
                                  {cost!==""&&blockchain.account !== null&&
                                    <div className='MintNum-top mt-4'>
                                      <Button onClick={() => { decrementMintAmount() }} className='mint-plus-btn   font-s bold   text-white'>
                                        -
                                      </Button>
                                      <input onChange={(e) => { setAmountInput(e.target.value) }} value={mintAmount} placeholder='1' className='para mintNum-input font-s  text-white' />
                                      <Button onClick={() => { incrementMintAmount() }} className='mint-plus-btn  font-s bold  text-white'>
                                        +
                                      </Button>
                                    </div>}

                                    {blockchain.account === "" || blockchain.smartContract === null ? (
                                        <Button className='mt-4 mintBtn' onClick={() => {connectWallet()}}>
                                          {ethereum===null?
                                          <img src={loading}></img>
                                          :paused===true?
                                            <span >
                                              MINT (Coming Soon)
                                            </span>
                                          :
                                            <span >
                                              Connect Wallet
                                            </span>
                                          }
                                        </Button>
                                      ) : (
                                        
                                      <Button className='mt-4  mintBtn' onClick={() => { mint() }} id='mint-btn' >
                                        <span>
                                          {claimingNft?'MINTING':'MINT'}
                                        </span>
                                        {claimingNft&& <img style={{marginLeft:'15px'}} src={loading}></img>}
                                      </Button>
                                      
                                    
                                    )}



                                    {/* <Button className='mt-2  mintBtn' >
                                      MINT
                                      <img src={loading}/>
                                    </Button> */}

                                   </div>
                                   <p className='para text-center mt-3'>
                                      **20 IS THE MAX # OF MINTS PER TRANSACTION
                                   </p>
                                 </div>
                            </div>
                         </div>
                    </div>
               </div>
          </div>
      </div>
  );
}

export default Mint;
